import React from 'react';
import map_picture from "./map.png";
import map_outline from "./map.svg";
import "./Map.css";
import { ReactSVG } from "react-svg";

const Map = () => {

    const handleAfterInjection = svg => {
        let paths = svg.getElementsByTagName('path');
        for (let i = 0; i < paths.length; i++) {
            paths[i].style.fill = 'rgba(0, 0, 0, 0.3)';
            paths[i].style.transition = 'fill 0.5s';
            paths[i].addEventListener('mouseover', function () {
                this.style.fill = 'transparent';
            });
            paths[i].addEventListener('mouseout', function () {
                this.style.fill = 'rgba(0, 0, 0, 0.3)';
            });
        }
    }

    return (
        <div className="w-full">
            <div className="relative w-full">
                <ReactSVG
                    src={map_outline}
                    className="map-outline"
                    beforeInjection={svg => {
                        svg.setAttribute('id', 'map-svg-outline');
                        svg.setAttribute('style', 'width: 100%; height: auto');
                        svg.setAttribute('preserveAspectRatio', 'xMidYMid meet');
                    }}
                    afterInjection={handleAfterInjection}
                />
                <img className="map-picture" src={map_picture} alt="" />
            </div>
        </div>
    );
}

export default Map;
